import React, { useContext } from 'react';
import { navigate } from "@reach/router";
import { makeStyles } from '@material-ui/core/styles';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import AppContext from '../../contextAPI/AppContext'
import KeyPassages from '../../data/KeyPassages.json'
import GetSegment from '../../scripts/NavigateToSegment'
import scrollToElement from 'scroll-to-element'

const useStyles = makeStyles(theme => ({
  root: {
    position: 'relative',
    display: 'flex',
    flexWrap: 'wrap',
    width: '100%'
  },
  formControl: {
    display: 'flex',
    flexWrap: 'nowrap',
    margin: theme.spacing(1),
    width: '75%',
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
}));

const spheres = {
  FOU: 'Foundational',
  CEL: 'Celebration',
  COM: 'Communication',
  ECO: 'Economics',
  EDU: 'Education',
  FAM: 'Family',
  GOV: 'Government',
  REL: 'Religion',
}

export default function SimpleSelect(props) {
  const receivedAppContext = useContext(AppContext)
  const classes = useStyles();
  const [sphereValue, setSphereValue] = React.useState((receivedAppContext.currentSphere !== '') ? receivedAppContext.currentSphere : '')
  const [keyPassageValue, setKeyPassageValue] = React.useState((receivedAppContext.currentKeyPassage !== '') ? receivedAppContext.currentKeyPassage : '')

  function handleSphereChange(event) {
    setSphereValue(event.target.value);
    receivedAppContext.updateSphere(event.target.value);
    receivedAppContext.updateKeyPassage('');
  }
  function handleKeyPassageChange(event) {
    setKeyPassageValue(event.target.value);
    receivedAppContext.updateKeyPassage(event.target.value);
    search(`${KeyPassages[`${receivedAppContext.currentSphere}-${event.target.value}`].ref}`)
  }

  function getKeyPassages(sphere) {
    let KPData = Object.values(KeyPassages).filter(p => p.spheres[0] === sphere)
    KPData = KPData.sort(function (a, b) {
      if (a.number < b.number) {
          return -1
      } else if (a.number > b.number) {
          return 1
      } else if (a.number === b.number) {
          return 0
      }
      // return b.val.localeCompare( a.val );
  });
    return KPData
  }

  function search (RefID) {
    const refInfo = RefID.split('-')
    console.log(RefID, refInfo)
    const segment = GetSegment(refInfo[0], parseInt(refInfo[1]), parseInt(refInfo[2]))
    receivedAppContext.updateDisplayRef(true)
    receivedAppContext.updateSearchID(RefID)
    if (receivedAppContext.currentSegmentID === segment) {
      props.closeMenu()
      console.log('same page navigation: did it work?')
      receivedAppContext.updateSegmentID(segment);
      samePageNavigation(`#ref-${RefID}`)
    } else {
      props.closeMenu()
      console.log('Did it navigate?')
      receivedAppContext.updateSegmentID(segment);
      navigate(`/${receivedAppContext.language}/${receivedAppContext.version}/${segment}`,
        { state: { currentInterlinear: receivedAppContext.interlinear, currentShowSpheres: receivedAppContext.showSpheres }})
    }
  }

  function samePageNavigation(id) {
    scrollToElement(id, {
        offset: -65,
        ease: 'inOutCube',
        duration: 1500
    });
    console.log('same page navigation')
    receivedAppContext.updateSearchID('')
  }

  return (
    <form className={classes.root} autoComplete="off">
      <FormControl className={classes.formControl}>
        <InputLabel htmlFor="age-simple">Sphere</InputLabel>
        <Select
          value={sphereValue}
          onChange={handleSphereChange}
        >
          {Object.keys(spheres).map(sphere => <MenuItem value={sphere}>{spheres[sphere]}</MenuItem>)}
        </Select>
      </FormControl>
      <FormControl className={classes.formControl}>
        <InputLabel htmlFor="age-simple">Key Passage</InputLabel>
        <Select
          value={keyPassageValue}
          onChange={handleKeyPassageChange}
        >
          {(receivedAppContext.currentSphere !== '') ? 
            getKeyPassages(receivedAppContext.currentSphere).sort().map(kp => {
              return <MenuItem style={(kp.ref.substring(0,3) !== 'Luk') ? {color: 'grey'} : null}value={kp.number}>{`#${kp.number}: ${kp.title}`}</MenuItem>
            })
            : <MenuItem value=''>Select Sphere First</MenuItem>}
        </Select>
      </FormControl>
    </form>
    );
}