import { Link } from "gatsby"
import PropTypes from "prop-types"
import React, { useContext, useState } from "react"
import AppContext from '../../contextAPI/AppContext'
import { navigate } from "@reach/router";

import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import Layers from '@material-ui/icons/Layers';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import Search from '@material-ui/icons/Search';
import SearchPanel from '../SearchPanel/SearchPanel'
import Menu from '../Menu/Menu'
import Drawer from '@material-ui/core/Drawer';
import SegmentTitles from '../../data/SegmentTitles.json'
import Typography from '@material-ui/core/Typography';
import Popover from '@material-ui/core/Popover';
import LayerSelect from '../LayerSelect/LayerSelect'
import SegmentSelect from '../AutoComplete/SegmentSelect'

const useStyles = makeStyles({
  toolBar: {
    justifyContent: 'stretch',
    // '& div': {
    //   justifyContent: 'space-evenly',
    //   border: 'red solid 2px',
    // }
  },
  icons: {
    margin: '3px'
  },
  title: {
    color: `white`,
    textDecoration: `none`,
    flex: '10'
  },
  buttonGroups: {
    flex: '1 1 100px',
    display: 'flex',
  },
  segmentSelect: {
    flex: '1 1 100px',
    position: 'relative',
    height: '100%',
    paddingTop: '6px',
    maxWidth: '50vw',
  },
  startButtons: {
    display: 'flex',
  },
  endButtons: {
    justifyContent: 'flex-end',
  },
  title: {
    flexGrow: 1,
    textAlign: 'center'
  },
  // title: {
  //   justifyContent: 'center',
  //   border: '1px red solid',
  //   fontSize: '120%',
  //   paddingTop: '10px'
  // },
})

const Header = ({ showIcons, currentLocation, segmentID }) => {
  const classes = useStyles();
  // This controls the visibility of chapter and verse references
  const receivedAppContext = useContext(AppContext);
  const [ menu, updateMenu ] = useState(false)
  const [ searchMenu, updateSearchMenu ] = useState(false)
  const interlinearOption = (receivedAppContext.version === 'GNA' || receivedAppContext.version === 'WLC') ? true : false
  
  //PopOver Layers toggling open and closed
  const [layerPopup, updateLayerPopup] = React.useState(null);

  function handleClick(event) {
    updateLayerPopup(event.currentTarget);
  }

  function layerHandleClose() {
    updateLayerPopup(null);
  }

  const layerOpen = Boolean(layerPopup);
  const layerId = layerOpen ? 'simple-popover' : undefined;

  return (
    <AppBar position="fixed">
    <Toolbar className={classes.toolBar}>
          <div className={`${classes.buttonGroups} ${classes.startButtons}`}>
            <IconButton className={classes.icons} size="small" color="inherit" aria-label="Menu" onClick={() => updateMenu(!menu)}>
              <MenuIcon />
            </IconButton>
          </div>
          <div className={`${classes.segmentSelect}`}>
          {(currentLocation.pathname !== '/') ? <SegmentSelect /> : <Typography variant="subtitle2" className={classes.title}>SourceView Reader</Typography>}
          </div>
          <div className={`${classes.buttonGroups} ${classes.endButtons}`}>
            {(receivedAppContext.AvailableContent && currentLocation.pathname !== '/') ? (<React.Fragment>
            {(receivedAppContext.AvailableContent[receivedAppContext.language].versions[receivedAppContext.version].layers.length > 0) ? (
            <React.Fragment>
              <IconButton className={classes.icons} size="small" color="inherit" aria-label="Menu" onClick={handleClick}>
                <Layers />
              </IconButton>
              <Popover
                id={layerId}
                open={layerOpen}
                anchorEl={layerPopup}
                onClose={layerHandleClose}
                anchorOrigin={{
                  vertical: 'bottom',
                  horizontal: 'center',
                }}
                transformOrigin={{
                  vertical: 'top',
                  horizontal: 'center',
                }}
              >
                <LayerSelect layers={receivedAppContext.AvailableContent[receivedAppContext.language].versions[receivedAppContext.version].layers} />
              </Popover>
            </React.Fragment>) : null}
            <IconButton className={classes.icons} size="small" color="inherit" aria-label="Menu" onClick={() => receivedAppContext.updateDisplayRef(!receivedAppContext.displayRef)}>
              {(receivedAppContext.displayRef) ? (<Visibility />) : 
              (<VisibilityOff />)}
            </IconButton>
            </React.Fragment>) : null}
            <IconButton className={classes.icons} size="small" color="inherit" aria-label="Menu" onClick={() => updateSearchMenu(!searchMenu)}>
              <Search />
            </IconButton>
          </div>
</Toolbar>
<Drawer 
  className={classes.drawer}
  open={menu} 
  anchor="left"
  width="75%"
  maxWidth="75%"
  overflowX="auto"
  onClose={() => updateMenu(false)}>
    <Menu location={currentLocation} closeMenu={() => updateMenu(false)} />
</Drawer>
<Drawer 
  className={classes.drawer}
  open={searchMenu} 
  anchor="right"
  width="75%"
  onClose={() => updateSearchMenu(false)}>
    <SearchPanel 
      currentLocation={currentLocation.pathname}
      closeSearchMenu={() => updateSearchMenu(false)}/>
</Drawer>
</AppBar>
)}

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
