import React, { useState } from "react"
import axios from 'axios'
import PropTypes from "prop-types"

import Header from "./header"
import NavButtons from "./navButtons"

import AppContext from '../../contextAPI/AppContext'

import { ThemeProvider } from '@material-ui/styles';
import theme from '../../theme'
import Container from '@material-ui/core/Container';

const WrapLayout = ({ children, location }) => {
  const hashRefs = (location.hash !== '') ? location.hash.split('-') : null
  const language = (hashRefs) ? hashRefs[0].substring(1,4) : 'ENG'
  const version = (hashRefs) ? hashRefs[1] : 'NLT'
  const URLsegment = (hashRefs) ? hashRefs[2] : 'S266'
  const book = (hashRefs) ? hashRefs[3] : 'Mat'
  const [ refID, updateRefID ] = useState((hashRefs) ? `${book}-${hashRefs[4]}-${hashRefs[5]}` : '')
  
  // -------------------------------------------------- //
  // -------------- STATE OF THE WEB APP -------------- //
  // -------------------------------------------------- //
  const [ currentVersion, updateVersion ] = useState(version)
  const [ currentLanguage, updateLanguage ] = useState(language)
  const [ currentSphere, updateSphere ] = useState('')
  const [ currentKeyPassage, updateKeyPassage ] = useState('')
  const [ currentSegmentID, updateSegmentID ] = useState(URLsegment)
  const [ currentBook, updateBook ] = useState(book)
  const [ languageSelect, updateLanguageSelect ] = useState(false)
  const [ versionSelect, updateVersionSelect ] = useState(false)
  const [ AvailableContent, updateAvailableContent ] = useState(null)
  if (!AvailableContent) {
    axios.get(`/AvailableContent.json`)
      .then(function(response) {
        updateAvailableContent(response.data.content)
      })
      .catch(error => console.log(error))
  }

  const [ searchID, updateSearchID ] = useState('')
  
  // keep DisplayRef user setting when changing pages
  let currentDisplayRef = (refID !== '') ? true : false
  if (location) {
    if (location.state !== null && location.state !== undefined){
      if (location.state.currentDisplayRef !== undefined) {
        currentDisplayRef = location.state.currentDisplayRef
      }
    }
  }
  const [ displayRef, updateDisplayRef ] = useState(currentDisplayRef)

  // keep interlinear user setting when changing pages
  let currentInterlinear = false
  if (location) {
    if (location.state !== null && location.state !== undefined){
      if (location.state.currentInterlinear !== undefined) {
        currentInterlinear = location.state.currentInterlinear
      }
    }
  }
  const [ interlinear, updateInterlinear ] = useState(currentInterlinear) 

  const toggleInterlinear = () => {
    if (interlinear) {
      updateInterlinear(false)
    } else {
      updateInterlinear(true)
      updateShowVerbs(false)
      updateShowSpheres(false)
    }
  }

  // keep showSpheres user setting when changing pages
  let currentShowSpheres = false
  if (location) {
    if (location.state !== null && location.state !== undefined){
      if (location.state.currentShowSpheres !== undefined) {
        currentShowSpheres = location.state.currentShowSpheres
      }
    }
  }
  const [ showSpheres, updateShowSpheres ] = useState(currentShowSpheres) 

  const toggleSpheres = () => {
    if (showSpheres) {
      updateShowSpheres(false)
    } else {
      updateInterlinear(false)
      updateShowVerbs(false)
      updateShowSpheres(true)
    }
  }

  // keep showVerbs user setting when changing pages
  let currentShowVerbs = false
  if (location) {
    if (location.state !== null && location.state !== undefined){
      if (location.state.currentShowVerbs !== undefined) {
        currentShowVerbs = location.state.currentShowVerbs
      }
    }
  }
  const [ showVerbs, updateShowVerbs ] = useState(currentShowVerbs) 

  const toggleVerbs = () => {
    if (showVerbs) {
      updateShowVerbs(false)
    } else {
      updateInterlinear(false)
      updateShowVerbs(true)
      updateShowSpheres(false)
    }
  }

  // -------------------------------------------------- //
  // ------------------- END OF STATE ----------------- //
  // -------------------------------------------------- //

  return (
    <ThemeProvider theme={theme}>
    <AppContext.Provider value={{
      AvailableContent,
      displayRef, 
      updateDisplayRef,
      interlinear,
      updateInterlinear: toggleInterlinear,
      showSpheres,
      updateShowSpheres: toggleSpheres,
      showVerbs,
      updateShowVerbs: toggleVerbs,
      version: currentVersion,
      updateVersion,
      language: currentLanguage,
      currentBook,
      updateBook,
      updateLanguage,
      currentSegmentID,
      updateSegmentID,
      searchID,
      updateSearchID,
      languageSelect,
      updateLanguageSelect: () => updateLanguageSelect(!languageSelect),
      versionSelect,
      updateVersionSelect: () => updateVersionSelect(!versionSelect),
      currentSphere,
      updateSphere,
      currentKeyPassage,
      updateKeyPassage,
      refID, 
      updateRefID,
    }}
      >
      <Header 
        interlinear
        version={currentVersion}
        showIcons={(location.pathname !== '/') ? true : false}
        currentLocation={location}
        segmentID={currentSegmentID}
      />
      <Container>
        <main refID={refID}>{children}</main>
      </Container>
      {(location.pathname !== '/') ? <NavButtons 
      location={location}
      // next={next} 
      // prev={prev}
      /> : null}
    </AppContext.Provider>
  </ThemeProvider>   
)}

WrapLayout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default WrapLayout
