import React, { useState, useContext } from 'react';
import { navigate } from "@reach/router";
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import BookChapterList from '../../data/BookChapterList.json';
import VerseCounts from '../../data/verseCounts.json';
import AppContext from '../../contextAPI/AppContext'
import GetSegment from '../../scripts/NavigateToSegment'
import Search from '@material-ui/icons/Search';
import scrollToElement from 'scroll-to-element'
import bibleBookList from '../../data/BookChapterList.json'

const useStyles = makeStyles(theme => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
    overflow: 'hidden',
    backgroundColor: theme.palette.background.paper,
    width: '75vw',
    height: 'auto',
    overflowY: 'auto'
  },
  bookOptions: {
    display: 'flex',
    width: '75vw',
    justifyContent: 'center',
    flexWrap: 'wrap',
    padding: '2px'
  },
  button: {
    margin: '3px',
    width: '15px'
  },
  actionPanel: {
    margin: '15px',
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'space-between',
    '& Button': {
        margin: '0 5px'
      }
  }
}));

export default function ImageGridList(props) {
  const classes = useStyles();

  // -------------------------------------------------- //
  // -------------- STATE OF THE WEB APP -------------- //
  // -------------------------------------------------- //
  const [ selectedBook, updateSelectedBook ] = useState('')
  const [ chapters, updateChapters ] = useState(['error'])
  const [ verses, updateVerses ] = useState(['error'])
  const [ selectedChapter, updateSelectedChapter ] = useState()
  const [ selectedVerse, updateSelectedVerse ] = useState(0)
  const receivedAppContext = useContext(AppContext)

  const bibleBookOrder = Object.keys(bibleBookList)
  const books = Object.keys(receivedAppContext.AvailableContent[receivedAppContext.language][receivedAppContext.version])

  books.sort(function(a, b) {
    return bibleBookOrder.indexOf(a) - bibleBookOrder.indexOf(b);
  });

  const pathSplit = props.currentLocation.split('/')
  const currentSegment = pathSplit[3]
  const selectBook = (book) => {
    updateSelectedBook(book)
    updateChapters([0,...BookChapterList[book].chapters.split(',')])
  }
  
  const deselectBook = () => {
    updateSelectedBook('')
    updateSelectedChapter(0)
    updateSelectedVerse(0) 
  }

  const selectChapter = (chapter) => {
    updateSelectedChapter(parseInt(chapter))
    const verseNums = []
    for (let i=0; i<VerseCounts[selectedBook][chapter]; i++) {
      verseNums.push(`${1+i}`)
    }
    updateVerses(verseNums)
  }

  const deselectChapter = () => {
    updateSelectedChapter()
    updateSelectedVerse(0) 
  }

  const selectVerse = (verse) => {
    updateSelectedVerse(parseInt(verse))
  }

  const deselectVerse = () => {
    updateSelectedVerse(0) 
  }

  function clearURL() {
    window.history.pushState({}, null, '/reader')
  }

  // ESCAPE THE NUMBER IN BOOKS STARTING WITH A NUMBER (1Ti, 2Th, 1Sa, 2Ch, etc.)
  const search = () => {
    const segment = GetSegment(selectedBook, selectedChapter, selectedVerse)
    receivedAppContext.updateDisplayRef(true)
    receivedAppContext.updateSearchID(`${selectedBook}-${selectedChapter}-${selectedVerse}`)
    if (currentSegment === segment) {
      props.closeSearchMenu()
      receivedAppContext.updateSegmentID(segment);
      clearURL()
      receivedAppContext.updateRefID('')
      // samePageNavigation(`#ref-${selectedBook}-${selectedChapter}-${selectedVerse}`)
    } else {
      props.closeSearchMenu()
      receivedAppContext.updateBook(selectedBook)
      receivedAppContext.updateSegmentID(segment);
      clearURL()
      receivedAppContext.updateRefID('')
      // samePageNavigation(`#ref-${selectedBook}-${selectedChapter}-${selectedVerse}`)
      // navigate(`/${receivedAppContext.language}/${receivedAppContext.version}/${segment}`,
      //   { state: { 
      //     currentInterlinear: receivedAppContext.interlinear, 
      //     currentShowSpheres: receivedAppContext.showSpheres,
      //     currentShowVerbs: receivedAppContext.showVerbs 
      //   }})
    }
  }

  // const samePageNavigation = (id) => {
  //   console.log('should be scrolling')
  //   console.log(receivedAppContext)
  //   scrollToElement(id, {
  //       offset: -65,
  //       ease: 'inOutCube',
  //       duration: 1500
  //   });
  // }

  return (
    <div className={classes.root}>
      <Typography display="block" >Select Book</Typography>
      {(selectedBook === '') ? (<div className={classes.bookOptions}>
        {books.map(book => (<Button color='secondary' variant="outlined" key={book} onClick={() => selectBook(book)}>{book}</Button>))}
      </div>) : (<div className={classes.bookOptions}>
        <Button color='secondary' variant="contained" key={selectedBook} onClick={() => deselectBook()}>{selectedBook}</Button>
      </div>)}
      {(selectedBook !== '') ? (
        <React.Fragment>
          <Typography display="block" >Select Chapter</Typography>
          <div className={classes.bookOptions}>
            {(!Number.isInteger(selectedChapter)) ? chapters.map(chapter => (<Button color='secondary' variant="outlined" key={chapter} onClick={() => selectChapter(parseInt(chapter))}>{(chapter !== 0) ? chapter : 'Int'}</Button>)) : <Button color='secondary' variant="contained" key={selectedChapter} onClick={() => deselectChapter()}>{(selectedChapter !== 0) ? selectedChapter : 'Int'}</Button>}
          </div>
        </React.Fragment>) : null}
      {(selectedBook !== '' && Number.isInteger(selectedChapter)) ? (
        <React.Fragment>
          {(selectedChapter !== 0) ? <Typography display="block" >Select Verse</Typography> : null}
          <div className={classes.bookOptions}>
            {(selectedVerse === 0) ? verses.map(verse => (<Button color='secondary' variant="outlined" key={verse} onClick={() => selectVerse(parseInt(verse))}>{verse}</Button>)) : <Button color='secondary' variant="contained" key={selectedVerse} onClick={() => deselectVerse()}>{selectedVerse}</Button>}
          </div>
        </React.Fragment>) : null}
        {(selectedVerse !== 0 || selectedChapter === 0) ? (<div className={classes.actionPanel}>
          <Button color='primary' variant="outlined" onClick={() => deselectBook()}>CLEAR</Button>
          <Button color='primary' variant="contained" onClick={() => search()}><Search/></Button>
        </div>) : null}
    </div>
  );
}