import React, { useContext, useState } from 'react'
import axios from 'axios'
import * as JSZip from 'jszip'
import { openDB, deleteDB, wrap, unwrap } from 'idb';
import AppContext from '../../contextAPI/AppContext'
import { navigate } from "@reach/router";

import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Button from '@material-ui/core/Button';
import Divider from '@material-ui/core/Divider';
import Home from '@material-ui/icons/Home';
import CheckCircle from '@material-ui/icons/CheckCircle';
import LanguageSelect from '../AutoComplete/LanguageSelect'
import VersionSelect from '../AutoComplete/VersionSelect'
import KeyPassageSelect from '../AutoComplete/KeyPassageSelect'
import Avatar from '@material-ui/core/Avatar';
import SVReaderLogo from '../../images/SVLogo.jpeg'
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Switch from '@material-ui/core/Switch';
import Popover from '@material-ui/core/Popover';

const useStyles = makeStyles(theme => ({
    root: {
      width: '100%',
      height: '100%',
      maxWidth: 300,
      backgroundColor: theme.palette.background.paper,
      overflowY: 'auto'
    },
    avatar: {
      marginRight: 10,
    },
    homeIcon: {
      left: 0
    },
    keyPassages: {
      display: 'flex',
      flexWrap: 'wrap',
      '& child': {
        margin: '2px'
      }
    },
    sphereSwitch: {
      width: '100%'
    },
    downloaded: {
      fontStyle: 'italic',
      color: 'grey'
    },
    offline: {
      marginTop: '10px',
      display: 'flex',
      justifyContent: 'space-between',
      verticalAlign: 'middle',
      padding: '2px',
      position: 'relative',
      width: '100%'
    },
    popover: {
      // padding: '20px',
      textAlign: 'center',
      maxWidth: '80vw'
    },
    popoverText: {
      padding: '20px',
      position: 'relative',
      width: '100%',
      fontWeight: 'bold',
    },
    options: {
      display: 'flex',
      justifyContent: 'space-around',
      padding: '20px'
    }
  }));


const Menu = (props) => {
  const classes = useStyles();
  const receivedAppContext = useContext(AppContext)
  const storage = window.localStorage;
  const [ database, updateDatabase ] = useState(0)
  const [ deleting, updateDeleting] = useState(false)

  //PopOver Layers toggling open and closed
  const [deleteDBPopup, updateDeleteDBPopup] = React.useState(false);

  function handleClick() {
    updateDeleteDBPopup(true);
  }

  function deleteDBHandleClose() {
    updateDeleteDBPopup(false);
  }

  const deleteDBOpen = Boolean(deleteDBPopup);
  const deleteDBId = deleteDBOpen ? 'simple-popover' : undefined;

  async function deleteCurrentVersion(language, version) {
    await deleteDB(`${language}-${version}`)
      .then(() => {
        updateDeleteDBPopup(false)
      })
  }
  async function downloadToDB(language, version) {
    const db = await openDB(`${language}-${version}`, 1, {
      upgrade(db) {
        // Create a store of objects
        const store = db.createObjectStore('segments', {
          // The 'id' property of the object will be the key.
          keyPath: 'id',
          // If it isn't explicitly set, create a value by auto incrementing.
          // autoIncrement: true,
        });
        // Create an index on the 'date' property of the objects.
        // store.createIndex('id', 'id');
      },
    });

    await axios.get(`/${language}/${version}/Bible.json`)
      .then(async (response) => {
        const segIDs = Object.keys(response.data)
        console.log(segIDs.length)
        await segIDs.forEach(async (seg) => {
          console.log(response.data[seg].id)
          await db.add('segments', response.data[seg]);
        })
      })
      .catch(e => console.log('error with IDB: ', e))
  }

  async function downloadVersion() {
    await downloadToDB(receivedAppContext.language,receivedAppContext.version)
      .then(() => {
        storage.setItem(`${receivedAppContext.language}-${receivedAppContext.version}`, true)
        updateDatabase(database + 1)
        updateDeleteDBPopup(false)
      })
  }

  async function deleteVersion() {
    updateDeleting(true)
    await deleteCurrentVersion(receivedAppContext.language,receivedAppContext.version)
      .then(() => {
        storage.removeItem(`${receivedAppContext.language}-${receivedAppContext.version}`)
        updateDatabase(database + 1)
        updateDeleteDBPopup(false)
        updateDeleting(false)
      })
  }

  function checkOffline() {
    const offline = (storage.getItem(`${receivedAppContext.language}-${receivedAppContext.version}`)) ? true : false
    console.log(offline)
    return offline
  }

    return (
        <div className={classes.root}>
            <List component="nav" aria-label="Main mailbox folders">
                <ListItem onClick={() => {
                        props.closeMenu()
                        receivedAppContext.updateSegmentID('');
                        navigate('/')
                    }} >
                    <Avatar alt="Remy Sharp" src={SVReaderLogo} className={classes.avatar} />
                    <ListItemText primary="SourceView Bible Reader" />
                    <ListItemIcon className={classes.homeIcon}>
                        <Home />
                    </ListItemIcon>
                </ListItem>
                <Divider />
                <ListItem className={classes.keyPassages}>
                    <LanguageSelect location={props.location} />
                    <VersionSelect location={props.location} />
                    <div className={classes.offline}>
                      {(!checkOffline()) ? <Button color="secondary" size="sm" variant="contained" onClick={() => downloadVersion()}>Download this version</Button> : 
                        (<React.Fragment>
                          <Typography className={classes.downloaded}>Downloaded</Typography>
                          <Button color="secondary" size="small" variant="outlined" onClick={() => handleClick()}>Remove</Button>
                          <Popover
                            className={classes.popover}
                            id={deleteDBId}
                            open={deleteDBOpen}
                            anchorEl={deleteDBPopup}
                            onClose={deleteDBHandleClose}
                            anchorOrigin={{
                              vertical: 'bottom',
                              horizontal: 'center',
                            }}
                            transformOrigin={{
                              vertical: 'top',
                              horizontal: 'center',
                            }}
                          >
                            {(!deleting) ? (<React.Fragment>
                                <Typography className={classes.popoverText}>Are you sure you want to remove this version from your device?</Typography>
                                <div className={classes.options}>
                                  <Button color="secondary" size="small" variant="outlined" onClick={() => deleteDBHandleClose()}>cancel</Button>
                                  <Button color="primary" size="small" variant="contained" onClick={() => deleteVersion()}>remove</Button>
                                </div>
                              </React.Fragment>) : <Typography className={classes.popoverText}>Please wait while deleting the version from your device...</Typography>}
                          </Popover>
                      </React.Fragment>)}
                    </div>
                </ListItem>
                {(receivedAppContext.AvailableContent[receivedAppContext.language].versions[receivedAppContext.version].layers.indexOf('spheres') !== -1) ? (<React.Fragment>
                    <Divider />
                    <ListItem className={classes.keyPassages}>
                        <FormGroup row className={classes.sphereSwitch}>
                            <FormControlLabel
                                label="Sphere Key Passages"
                                labelPlacement="start"
                                control={
                                <Switch checked={receivedAppContext.showSpheres} onChange={() => receivedAppContext.updateShowSpheres(!receivedAppContext.showSpheres)} value={receivedAppContext.showSpheres} />
                                }
                            />
                        </FormGroup>
                    </ListItem>
                    {(receivedAppContext.showSpheres) ? (<ListItem><KeyPassageSelect closeMenu={props.closeMenu} /></ListItem>) : null}
                </React.Fragment>) : null}
                <Divider />
            </List>
        </div>)
}

export default Menu;