import React, { useContext }  from 'react';
import { navigate } from "@reach/router";
import AppContext from '../../contextAPI/AppContext'
import clsx from 'clsx';
import Select from 'react-select';
import { emphasize, makeStyles, useTheme } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import NoSsr from '@material-ui/core/NoSsr';
import TextField from '@material-ui/core/TextField';
import Paper from '@material-ui/core/Paper';
import Chip from '@material-ui/core/Chip';
import MenuItem from '@material-ui/core/MenuItem';
import CancelIcon from '@material-ui/icons/Cancel';
import PropTypes from 'prop-types';
import bibleBookList from '../../data/BookChapterList.json'

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    height: 'auto',
  },
  input: {
    display: 'flex',
    padding: 0,
    height: 'auto',
  },
  valueContainer: {
    display: 'flex',
    flexWrap: 'wrap',
    flex: 1,
    alignItems: 'center',
    overflow: 'hidden',
  },
  chip: {
    margin: theme.spacing(0.5, 0.25),
  },
  chipFocused: {
    backgroundColor: emphasize(
      theme.palette.type === 'light' ? theme.palette.grey[300] : theme.palette.grey[700],
      0.08,
    ),
  },
  noOptionsMessage: {
    padding: theme.spacing(1, 2),
  },
  singleValue: {
    fontSize: 16,
  },
  placeholder: {
    position: 'absolute',
    left: 2,
    bottom: 6,
    fontSize: 16,
  },
  paper: {
    position: 'absolute',
    zIndex: 1,
    marginTop: theme.spacing(1),
    left: 0,
    right: 0,
  },
  divider: {
    height: theme.spacing(2),
  },
}));

function NoOptionsMessage(props) {
  return (
    <Typography
      color="textSecondary"
      className={props.selectProps.classes.noOptionsMessage}
      {...props.innerProps}
    >
      {props.children}
    </Typography>
  );
}

NoOptionsMessage.propTypes = {
  children: PropTypes.node,
  innerProps: PropTypes.object,
  selectProps: PropTypes.object.isRequired,
};

function inputComponent({ inputRef, ...props }) {
  return <div ref={inputRef} {...props} />;
}

inputComponent.propTypes = {
  inputRef: PropTypes.oneOfType([PropTypes.func, PropTypes.object]),
};

function Control(props) {
  const {
    children,
    innerProps,
    innerRef,
    selectProps: { classes, TextFieldProps },
  } = props;

  return (
    <TextField
      fullWidth
      InputProps={{
        inputComponent,
        inputProps: {
          className: classes.input,
          ref: innerRef,
          children,
          ...innerProps,
        },
      }}
      {...TextFieldProps}
    />
  );
}

Control.propTypes = {
  children: PropTypes.node,
  innerProps: PropTypes.object,
  innerRef: PropTypes.oneOfType([PropTypes.func, PropTypes.object]),
  selectProps: PropTypes.object.isRequired,
};

function Option(props) {
  return (
    <MenuItem
      ref={props.innerRef}
      selected={props.isFocused}
      component="div"
      style={{
        fontWeight: props.isSelected ? 500 : 400,
      }}
      {...props.innerProps}
    >
      {props.children}
    </MenuItem>
  );
}

Option.propTypes = {
  children: PropTypes.node,
  innerProps: PropTypes.object,
  innerRef: PropTypes.oneOfType([PropTypes.func, PropTypes.object]),
  isFocused: PropTypes.bool,
  isSelected: PropTypes.bool,
};

function Placeholder(props) {
  return (
    <Typography
      color="textSecondary"
      className={props.selectProps.classes.placeholder}
      {...props.innerProps}
    >
      {props.children}
    </Typography>
  );
}

Placeholder.propTypes = {
  children: PropTypes.node,
  innerProps: PropTypes.object,
  selectProps: PropTypes.object.isRequired,
};

function SingleValue(props) {
  return (
    <Typography className={props.selectProps.classes.singleValue} {...props.innerProps}>
      {props.children}
    </Typography>
  );
}

SingleValue.propTypes = {
  children: PropTypes.node,
  innerProps: PropTypes.object,
  selectProps: PropTypes.object.isRequired,
};

function ValueContainer(props) {
  return <div className={props.selectProps.classes.valueContainer}>{props.children}</div>;
}

ValueContainer.propTypes = {
  children: PropTypes.node,
  selectProps: PropTypes.object.isRequired,
};

function MultiValue(props) {
  return (
    <Chip
      tabIndex={-1}
      label={props.children}
      className={clsx(props.selectProps.classes.chip, {
        [props.selectProps.classes.chipFocused]: props.isFocused,
      })}
      onDelete={props.removeProps.onClick}
      deleteIcon={<CancelIcon {...props.removeProps} />}
    />
  );
}

MultiValue.propTypes = {
  children: PropTypes.node,
  isFocused: PropTypes.bool,
  removeProps: PropTypes.object.isRequired,
  selectProps: PropTypes.object.isRequired,
};

function Menu(props) {
  return (
    <Paper square className={props.selectProps.classes.paper} {...props.innerProps}>
      {props.children}
    </Paper>
  );
}

Menu.propTypes = {
  children: PropTypes.node,
  innerProps: PropTypes.object,
  selectProps: PropTypes.object,
};

const components = {
  Control,
  Menu,
  MultiValue,
  NoOptionsMessage,
  Option,
  Placeholder,
  SingleValue,
  ValueContainer,
};

export default function IntegrationReactSelect(props) {
  const receivedAppContext = useContext(AppContext)
  console.log(receivedAppContext)
  const classes = useStyles();
  const theme = useTheme();
  const [single, setSingle] = React.useState(null);
  if (single === null) {
    setSingle({
      label: receivedAppContext.AvailableContent[receivedAppContext.language].language_name,
      value: receivedAppContext.language
    });
  }

  function toggleLayersOff() {
    if (receivedAppContext.showSpheres) {
      receivedAppContext.updateShowSpheres() 
    }
    if (receivedAppContext.showVerbs) {
      receivedAppContext.updateShowVerbs()
    }
    if (receivedAppContext.interlinear) {
      receivedAppContext.updateInterlinear()
    }
  }

  const getLangOptions = () => {
    let LanguageOptions = []
    const languageKeys = Object.keys(receivedAppContext.AvailableContent)
    languageKeys.forEach(key => {
        const langObj = {
            label: receivedAppContext.AvailableContent[key].language_name,
            value: key
        }
        LanguageOptions.push(langObj)
    })
    return LanguageOptions
  }

  function handleChangeSingle(value) {
    setSingle(value);
    updateLang(value.value);
  }
  
  function sortBooks (books) {
    const bibleBookOrder = Object.keys(bibleBookList)
    books.sort(function(a, b) {
      return bibleBookOrder.indexOf(a) - bibleBookOrder.indexOf(b);
    });
    return books
  }

  const updateLang = (value) => {
    receivedAppContext.updateLanguage(value)
    receivedAppContext.updateVersion(getVersionOptions(value)[0].value)
    receivedAppContext.updateLanguageSelect(false)
    toggleLayersOff()
    if (props.location.pathname !== '/') {
      const booksOfNewLanguage = Object.keys(receivedAppContext.AvailableContent[value][getVersionOptions(value)[0].value])
      const firstBookOfNewLanguage = sortBooks(booksOfNewLanguage)[0]
      const firstSegmentOfNewLanguage = receivedAppContext.AvailableContent[value][getVersionOptions(value)[0].value][firstBookOfNewLanguage].segments[1];
      receivedAppContext.updateSegmentID(firstSegmentOfNewLanguage);
    }
  }

  const getVersionOptions = (langKey) => {
    let VersionOptions = []
    const versionKeys = Object.keys(receivedAppContext.AvailableContent[langKey].versions)
    versionKeys.forEach(key => {
        const versionObj = {
            label: receivedAppContext.AvailableContent[langKey].versions[key],
            value: key
        }
        VersionOptions.push(versionObj)
    })
    return VersionOptions
  }

  const revealVersionSelect = () => {
    receivedAppContext.updateLanguageSelect(false)
    receivedAppContext.updateVersionSelect(true)
  }

  const selectStyles = {
    input: base => ({
      ...base,
      color: theme.palette.text.primary,
      '& input': {
        font: 'inherit',
      },
    }),
  };

  return (
    <div className={classes.root}>
      <NoSsr>
        <Select
          classes={classes}
          styles={selectStyles}
          inputId="react-select-single"
          TextFieldProps={{
            label: 'Language',
            InputLabelProps: {
              htmlFor: 'react-select-single',
              shrink: true,
            },
            placeholder: 'Search Languages',
          }}
          options={getLangOptions()}
          components={components}
          value={single}
          onChange={handleChangeSingle}
        />
      </NoSsr>
    </div>
  );
} 