import React, { useContext } from 'react'
import { navigate } from "@reach/router";
import AppContext from '../../contextAPI/AppContext'

import { makeStyles } from '@material-ui/styles';
import IconButton from '@material-ui/core/IconButton';
import PlayCircleFilled from '@material-ui/icons/PlayCircleFilled';
import SegmentTitles from '../../data/SegmentTitles.json'

const useStyles = makeStyles({
    link: {
        position: 'fixed',
        bottom: '0.3em',
        paddingTop: '0em',
        paddingBottom: '0.14em'
    },
    left: {
        left: '0.3em',
        paddingLeft: '0.13em',
        paddingRight: '0.17em'
    },
    right: {
        right: '0.3em',
        paddingLeft: '0.17em',
        paddingRight: '0.13em'
    },
    icon: {
        backgroundColor: 'white', 
        color: '#3A4C7D', 
        borderRadius: '25px',
        fontSize: '40px',

    }
})

function clearURL() {
    window.history.pushState({}, null, '/reader')
  }

const IconButtons = (props) => {
  const receivedAppContext = useContext(AppContext)
  const classes = useStyles();

  // This set of code sets the links for the previous and next buttons of the navigation
  const checkSegmentExists = (segmentID) => {
    const books = Object.keys(receivedAppContext.AvailableContent[receivedAppContext.language][receivedAppContext.version])
    let exists = false
    books.forEach(book => {
      if (receivedAppContext.AvailableContent[receivedAppContext.language][receivedAppContext.version][book].segments.indexOf(segmentID) !== -1) {
        exists = book
      }
    })
    return exists
  }

  const checkNavSegmentExists = (currentSeg, direction) => {
    const segmentIDs = Object.keys(SegmentTitles)
    const index = segmentIDs.indexOf(currentSeg)
    const next = checkSegmentExists(segmentIDs[index + 1])
    const prev = checkSegmentExists(segmentIDs[index - 1])
    if (direction === 'next' && next) {
      receivedAppContext.updateBook(next)
      receivedAppContext.updateSegmentID(segmentIDs[index + 1])
      clearURL()
      receivedAppContext.updateRefID('top')
    } else if (direction === 'prev' && prev) {
      receivedAppContext.updateBook(prev)
      receivedAppContext.updateSegmentID(segmentIDs[index - 1])
      clearURL()
      receivedAppContext.updateRefID('top')
    } else {
      navigate(`/`,
      { state: 
        { 
          currentInterlinear: receivedAppContext.interlinear, 
          currentShowSpheres: receivedAppContext.showSpheres,
          currentShowVerbs: receivedAppContext.showVerbs 
        }
      }
      )
    }
  }

    return (
        <React.Fragment>
            <div className={`${classes.link} ${classes.left}`}  
              onClick={() => checkNavSegmentExists(receivedAppContext.currentSegmentID,'prev')}
            >
                <IconButton>
                    <PlayCircleFilled 
                        className={classes.icon}
                        style={{transform: 'rotate(180deg)'}} />
                </IconButton>
            </div>
            <div
              className={`${classes.link} ${classes.right}`}
              onClick={() => checkNavSegmentExists(receivedAppContext.currentSegmentID,'next')}
            >
              <IconButton>
                  <PlayCircleFilled 
                      className={classes.icon}/>
              </IconButton>
            </div>
        </React.Fragment>
    )
}

export default IconButtons; 