import React, { useContext, useState, useEffect } from 'react';
import { navigate } from "@reach/router";
import { makeStyles } from '@material-ui/core/styles';
import InputBase from '@material-ui/core/InputBase';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import AppContext from '../../contextAPI/AppContext'
import SortBooks from '../../scripts/sortBooksByBibleOrder'
import SegmentTitles from '../../data/SegmentTitles.json'
import GetBookOfSegment from '../../scripts/getBookOfSegment'

const useStyles = makeStyles(theme => ({
  root: {
    position: 'relative',
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    width: '100%',
    height: '100%',
    color: 'white',
    margin: 0,
    textOverflow: 'ellipsis',
    // maxWidth: 180,
  },
  form: {
    margin: 0,
    minWidth: 120,
  },
  formControl: {
    margin: 0,
  },
  select: {
    color: 'white',
    WebkitAppearance: 'none',
    textIndent: '1px',
    textAlign: 'center',
    textOverflow: '',
    '&:before': {
      WebkitAppearance: 'none',
      // borderColor: 'white',
    },
    '&:after': {
      WebkitAppearance: 'none',
      // borderColor: 'white',
    }
  },
  icon: {
      fill: 'white',
  },
}));

export default function SimpleSelect() {
  const receivedAppContext = useContext(AppContext)
  const classes = useStyles();
  const [segmentDisplay, updateSegmentDisplay] = useState(receivedAppContext.currentSegmentID)

  useEffect(() => {
    updateSegmentDisplay(receivedAppContext.currentSegmentID)
  }, [receivedAppContext.currentSegmentID])

  function clearURL() {
    window.history.pushState({}, null, '/reader')
  }

  function handleSegmentChange(event) {
    clearURL()
    const bookOfSeg = GetBookOfSegment(event.target.value, receivedAppContext)
    if (bookOfSeg) {
      receivedAppContext.updateBook(bookOfSeg)
    }
    receivedAppContext.updateSegmentID(event.target.value);
    receivedAppContext.updateRefID('top');
    updateSegmentDisplay(event.target.value)
    // navigate(`/${receivedAppContext.language}/${receivedAppContext.version}/${event.target.value}`,
    //     { state: { 
    //       currentInterlinear: receivedAppContext.interlinear, 
    //       currentShowSpheres: receivedAppContext.showSpheres,
    //       currentShowVerbs: receivedAppContext.showVerbs 
    //     }})
  }

  function listAvailableSegments(language, version) {
    let books = SortBooks(Object.keys(receivedAppContext.AvailableContent[language][version]))
    let segments = []
    books.forEach(book => {
      segments = [...segments, ...receivedAppContext.AvailableContent[language][version][book].segments]
    })
    return segments
  }

  return (
    <div>
      {(!receivedAppContext.AvailableContent) ? null : (
        <form className={classes.root} autoComplete="off">
        <FormControl variant="outlined" className={classes.formControl}>
          <Select
            value={segmentDisplay}
            onChange={handleSegmentChange}
            className={classes.select}
            inputProps={{
              classes: {
                icon: classes.icon,
              },
            }}
            input={<InputBase
              className={classes.input}
            />}
          >
            {listAvailableSegments(receivedAppContext.language, receivedAppContext.version).map(segment => {
              if (segment.substring(0,1) === 'I') {
                return <MenuItem value={segment}>{SegmentTitles[segment].title}</MenuItem>
              } else if (segment.substring(0,1) === 'S') {
                return <MenuItem value={segment}>{`${SegmentTitles[segment].book[0]} ${SegmentTitles[segment].ref}`}</MenuItem>
              } else {
                console.log('error in list of segments', typeof segment)
              }
            })}
          </Select>
        </FormControl>
      </form>
      )}
    </div>
    );
}