import React, { useState, useContext } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import AppContext from '../../contextAPI/AppContext'

const useStyles = makeStyles(theme => ({
  root: {
    display: 'flex',
  },
  formControl: {
    margin: theme.spacing(3),
  },
  group: {
    margin: theme.spacing(1, 0),
  },
}));

export default function RadioButtonsGroup(props) {
  const classes = useStyles();
  const receivedAppContext = useContext(AppContext);

  function getLayer() {
    if (receivedAppContext.showSpheres) {
      return "SphereView"
    }
    if (receivedAppContext.showVerbs) {
      return "VerbView"
    }
    if (receivedAppContext.interlinear) {
      return "Interlinear"
    }
    return ''
  }

  const [value, setValue] = useState(getLayer());

  function toggleLayersOff() {
    if (receivedAppContext.showSpheres) {
      receivedAppContext.updateShowSpheres() 
    }
    if (receivedAppContext.showVerbs) {
      receivedAppContext.updateShowVerbs()
    }
    if (receivedAppContext.interlinear) {
      receivedAppContext.updateInterlinear()
    }
  }

  function handleChange(event) {
    const layer = event.target.value
    setValue(layer);
    if (layer === '') {
      toggleLayersOff()
    } 
    else if (layer === 'SphereView') {
      receivedAppContext.updateShowSpheres() 
    }
    else if (layer === 'VerbView') {
      receivedAppContext.updateShowVerbs()
    }
    else if (layer === 'Interlinear') {
      receivedAppContext.updateInterlinear()
    }
    
  }

  return (
    <div className={classes.root}>
      <FormControl component="fieldset" className={classes.formControl}>
        <FormLabel component="legend">SourceView Layers</FormLabel>
        <RadioGroup
          aria-label="gender"
          name="gender1"
          className={classes.group}
          value={value}
          onChange={handleChange}
        >
          <FormControlLabel value="" control={<Radio />} label="SourceView Only" />
          {(props.layers.indexOf('spheres') !== -1) ? <FormControlLabel value="SphereView" control={<Radio />} label="SphereView" /> : null}
          {(props.layers.indexOf('verbs') !== -1) ? <FormControlLabel value="VerbView" control={<Radio />} label="VerbView" /> : null}
          {(props.layers.indexOf('interlinear') !== -1) ? <FormControlLabel value="Interlinear" control={<Radio />} label="InterlinearView" /> : null}
        </RadioGroup>
      </FormControl>
    </div>
  );
}